:root {
  --font-black: #000;
  --font-dark: #232323;
  --font-gray: #464646;
  --card-beige: #FAF0EA;
  --footer: #F6E7DA;
}

* {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  font-family: "Montserrat", sans-serif;
  margin: 0 auto;
}

a {
  color: var(--font-black);
  transition: all 0.2s ease-in;
  text-decoration: none;
}
a:hover {
  color: var(--font-gray);
}

h3 {
  color: var(--font-black);
  font-family: Montserrat;
  font-size: 32px;
  font-weight: 600;
  line-height: 120%;
  letter-spacing: 0;
}

.container {
  max-width: 1600px;
  padding: 0 200px;
}

.bold {
  font-weight: bold;
}

.map-iframe {
  width: 590px;
  height: 367px;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 114px;
  padding-top: 10px;
  margin: auto;
}
.header a {
  text-decoration: none;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-menu {
  display: flex;
  margin-right: 36px;
}
.header-menu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 43px;
  padding-left: 0;
}
.header-menu::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.2);
  transform: translate(18px, 13px);
}

.header-phones {
  font-weight: 600;
  color: var(--font-black);
}

.header-search {
  cursor: pointer;
}

.main {
  margin: 0 auto;
  width: 100%;
}

.main-hero {
  width: 100%;
  height: 629px;
  background-image: url("../../assets/bg/impuls-bg.jpg");
  background-position: center center;
  background-size: cover;
  color: var(--font-dark);
}

.main-hero__headers {
  padding-top: 192px;
}

.main-hero__head1 {
  font-size: 64px;
  line-height: 150%;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 40px;
  margin-top: 0;
}

.main-hero__head2 {
  font-weight: 500;
  font-size: 36px;
  max-width: 484px;
  line-height: 150%;
}

.main-advantages {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 40px auto 25px;
  margin-top: 40px;
}

.advantages-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.advantages-item__img {
  width: 50px;
  height: 50px;
}

.main-catalog {
  margin: 0 auto;
}

.catalog-items__top {
  display: grid;
  grid-template-columns: repeat(3, 386px);
  grid-template-rows: repeat(3, 210px);
  justify-items: center;
  gap: 20px 20px;
  grid-template-areas: "A B C" "A D E" "G F E";
}
.catalog-items__top .catalog-item:first-child {
  flex-direction: column-reverse;
  justify-content: space-between;
  padding-top: 130px;
}
.catalog-items__top .catalog-item:first-child .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__top .catalog-item:first-child .catalog-item__image-container img {
  max-width: 270px;
  max-height: 200px;
}
.catalog-items__top .catalog-item:nth-child(1) {
  grid-area: A;
}
.catalog-items__top .catalog-item:nth-child(2) {
  grid-area: B;
}
.catalog-items__top .catalog-item:nth-child(2) .catalog-item__image-container img {
  max-height: 140px;
}
.catalog-items__top .catalog-item:nth-child(3) {
  grid-area: C;
}
.catalog-items__top .catalog-item:nth-child(3) .catalog-item__image-container img {
  max-height: 140px;
}
.catalog-items__top .catalog-item:nth-child(4) {
  grid-area: D;
  gap: 76px;
}
.catalog-items__top .catalog-item:nth-child(4) img {
  max-height: 140px;
}
.catalog-items__top .catalog-item:nth-child(5) {
  grid-area: E;
}
.catalog-items__top .catalog-item:nth-child(5) img {
  max-height: 320px;
}
.catalog-items__top .catalog-item:nth-child(6) {
  grid-area: G;
  flex-direction: column-reverse;
  gap: 0px;
}
.catalog-items__top .catalog-item:nth-child(6) .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__top .catalog-item:nth-child(7) {
  grid-area: F;
  gap: 20px;
}
.catalog-items__top .catalog-item:nth-child(7) img {
  max-height: 150px;
}

.catalog-items__bottom {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  justify-content: space-between;
}
.catalog-items__bottom .catalog-item {
  width: 285px;
  height: 210px;
}
.catalog-items__bottom .catalog-item:nth-child(1) {
  gap: 15px;
}
.catalog-items__bottom .catalog-item:nth-child(1) img {
  max-height: 150px;
}
.catalog-items__bottom .catalog-item:nth-child(2) {
  flex-direction: column-reverse;
  gap: 0;
}
.catalog-items__bottom .catalog-item:nth-child(2) .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__bottom .catalog-item:nth-child(2) .catalog-item__info h4 {
  margin-top: 10px;
}
.catalog-items__bottom .catalog-item:nth-child(3) {
  gap: 0;
}
.catalog-items__bottom .catalog-item:nth-child(3) img {
  max-height: 150px;
}
.catalog-items__bottom .catalog-item:nth-child(4) {
  flex-direction: column-reverse;
  gap: 0;
}
.catalog-items__bottom .catalog-item:nth-child(4) .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__bottom .catalog-item:nth-child(4) img {
  max-height: 80px;
}

.catalog-item {
  width: 386px;
  display: flex;
  flex-direction: row;
  background-color: #fdf8f5;
  justify-content: flex-start;
  align-items: center;
  padding: 24px;
  gap: 40px;
}
.catalog-item .catalog-item__info {
  align-self: flex-end;
}
.catalog-item .catalog-item__info a {
  text-decoration: none;
}

.catalog-item__image-container {
  max-width: 370px;
}
.catalog-item__image-container img {
  max-width: 200px;
  max-height: 100px;
}

.catalog-item__info h4 {
  margin-bottom: 6px;
}

.main-individual {
  margin: 0 auto;
}

.individual-hero {
  background-image: url("../assets/bg/individual-bg.jpeg");
  background-position: center center;
  width: 100%;
  height: 440px;
  display: flex;
  align-items: center;
  padding: 100px;
  margin-top: 106px;
}
.individual-hero h2 {
  font-size: 48px;
  line-height: 150%;
  color: var(--font-dark);
  font-weight: 600;
  letter-spacing: 0%;
}

.main-news {
  margin: 0 auto;
}

.news-container {
  margin-top: 90px;
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}
.news-container h3 {
  font-size: 32px;
  font-weight: 600;
  line-height: 120%;
}

.news-items {
  align-self: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 20px;
}

.news-item {
  max-width: 386px;
  display: flex;
  flex-direction: column;
}

.news-item__title {
  margin: 16px 0;
  font-size: 16px;
  letter-spacing: 0%;
  line-height: 20px;
}

.news-item__info {
  font-size: 12px;
  margin-top: 0;
  line-height: 150.5%;
}

.news-item__link-date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  font-size: 12px;
}

.news-item__link,
.news-item__date {
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0%;
  font-size: 12px;
}

.section-contacts {
  margin: 0 auto;
}

.contacts-container {
  margin: 120px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contacts-info__head {
  margin-bottom: 50px;
}

.contacts-info__data {
  display: flex;
  flex-direction: column;
  color: var(--font-dark);
}
.contacts-info__data a {
  color: var(--font-dark);
  line-height: 180%;
}
.contacts-info__data a:hover {
  color: var(--font-black);
  font-weight: 500;
}

.contacts-info__social a {
  margin-right: 17px;
}

.footer {
  width: 100%;
  background-color: var(--footer);
  padding-top: 90px;
}

.footer_top {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.contact-form {
  display: flex;
  flex-direction: column;
  max-width: 385px;
}

.contact-form__input-text {
  border: none;
  border-bottom: 1px solid var(--font-dark);
  height: 50px;
  background-color: var(--footer);
  padding-left: 5px;
}

.contact-form__input-button {
  height: 50px;
  background-color: rgb(187, 144, 120);
  border: none;
  color: white;
  font-style: 16px;
  font-weight: 600;
  line-height: 180%;
  letter-spacing: 0%;
  margin-top: 20px;
}

.footer-menu h5 {
  margin-top: 0;
}
.footer-menu ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  gap: 20px;
  padding: 0;
}